import {
  moveEvent,
  endEvent,
  getPositionOffset,
  getClientPosition,
  isOutOfBrowser,
  judgeResizeType,
  getSize,
  setSize,
  calDistance,
} from './common';
import constant from '../config/constant';

/**
 * 
 *
 * @param {Object} touchStartPoint /
 * @param {Object} touchEndPoint /
 * @param {Object} windowOriginPosition 
 * @param {Object} movable /
 */
function _calWindowCurrentPosition(
  touchStartPoint,
  touchEndPoint,
  windowOriginPosition,
  movableParam
) {
  const supposePosition = {
    x: touchEndPoint.x - touchStartPoint.x + windowOriginPosition.x,
    y: touchEndPoint.y - touchStartPoint.y + windowOriginPosition.y,
  };

  switch (movableParam) {
    case 'horizontal':
      return {
        x: supposePosition.x,
        y: windowOriginPosition.y,
      };

    case 'vertical':
      return {
        x: windowOriginPosition.x,
        y: supposePosition.y,
      };

    case true:
    default:
      return supposePosition;
  }
}

export function handleStartEventForMove(event) {
  function _handleEndEventForMove(event) {
    /*  */
    moveEndCallback();
    nowInMoving = false;

    document.removeEventListener(moveEvent, _handleMoveEventForMove, false); // 

    event.preventDefault();

    /* movingclicksetTimeoutmovingclick */
    setTimeout(() => {
      window.className = window.className.replace(/ ?moving/, '');
    }, 0);
  }

  function _handleMoveEventForMove(event) {
    /* window */
    if (isOutOfBrowser(event)) {
      return false;
    }

    const position = getClientPosition(event); // /
    /*  */
    const positionOffset = _calWindowCurrentPosition(
      startPoint,
      position,
      originPositionOffset,
      movableParam
    );

    window.style.top = positionOffset.y + 'px'; // top
    window.style.left = positionOffset.x + 'px'; // left
    window.style.bottom = 'auto'; // auto
    window.style.right = 'auto'; // auto

    /* movingclick */
    if (
      calDistance({
        x1: position.x,
        y1: position.y,
        x2: startPoint.x,
        y2: startPoint.y,
      }) > constant.AVAILABLE_CLICK_MAX_MOVE_DISTANCE &&
      window.className.indexOf('moving') === -1
    ) {
      window.className += ' moving';

      /*  */
      if (!nowInMoving) {
        moveStartCallback();
        nowInMoving = true; // moveStartCallback
      }
      movingCallback();
    }
  }

  const window = this.window;
  const startPoint = getClientPosition(event); // 
  const movableParam = this.params.movable;
  const moveStartCallback = this.params.moveStartCallback;
  const movingCallback = this.params.movingCallback;
  const moveEndCallback = this.params.moveEndCallback;
  let nowInMoving = false;

  /* MoveHandlerresizeresize */
  if (
    this.params.resizable &&
    this.isMoveHandlerEqualWindow &&
    judgeResizeType(startPoint, window) !== 'middle'
  ) {
    return;
  }

  /*  */
  let size = getSize(window);
  size = {
    width: parseInt(size.width),
    height: parseInt(size.height),
  };
  setSize(window, size.width, size.height);

  document.addEventListener(moveEvent, _handleMoveEventForMove, false); // 
  document.addEventListener(endEvent, _handleEndEventForMove);

  const originPositionOffset = getPositionOffset(window); // 

  event.preventDefault();
}
